.footer {
  background-color: $gray-bg;
}

.footer__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 42px 20px 30px;
}

.footer-top {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  padding-bottom: 60px;
}

.footer-bottom {
  text-align: center;
}

.footer-bottom__link {
  padding: 4px 8px;
  font-size: 12px;
  transition: all 0.2s ease-in;
  &:not(:last-child) {
    margin-right: 81px;
  }
  &:hover {
    color: $blue;
  }
  &:active {
    color: $blue-down;
  }
  &:focus-visible {
    outline: none;
    box-shadow: 0px 0px 2px rgba($blue, 0.8) inset;
    color: $blue;
    border-radius: 3px;
  }
}

// ----------------------------- mailing
.mailing {
  max-width: 280px;
}

.mailing__title {
  max-width: 230px;
  margin-bottom: 34px;
  font-size: 14px;
  font-weight: 700;
}

.mailing__form {
  display: flex;
}

.mailing__input {
  padding: 8px 12px;
  border: none;
  background-color: $white;
  font-size: 14px;
  line-height: 1.2;
  &::placeholder {
    font-family: "SF Pro Display", "Arial", sans-serif;
    color: #c4c4c4;
  }
  &:focus-visible {
    outline: none;
    box-shadow: 0px 0px 5px 1px $blue inset;
  }
}

.mailing__button {
  border: none;
  padding: 10px 16px;
  background-color: $blue;
  color: $white;
  text-transform: uppercase;
  font-size: 11px;
  line-height: 13px;
  font-weight: 600;
  letter-spacing: 0.12em;
  transition: all 0.2s ease-in;
  cursor: pointer;
  &:hover {
    background-color: rgba($blue, 0.8);
  }
  &:active {
    background-color: rgba($blue, 0.6);
    color: $black;
  }
  &:focus-visible {
    outline: none;
    background-color: $white;
    color: $blue;
    box-shadow: 0px 0px 5px 1px $blue inset;
  }
}

// ----------------------------------- info, store
.footer__info,
.footer__store {
  max-width: 200px;
}

.footer-menu__icon {
  display: none;
}

.footer__drop-down {
  padding: 0 4px;
  margin-bottom: 4px;
  &:focus-visible {
    outline: none;
    box-shadow: 0px 0px 2px rgba($blue, 0.8) inset;
    color: $blue;
    border-radius: 3px;
  }
}

.footer-menu__title {
  font-size: 14px;
  font-weight: 700;
}

.footer-menu__item {
  margin-bottom: 3px;
}

.footer-menu__link {
  display: inline-block;
  width: 100%;
  padding: 0 4px;
  transition: all 0.2s ease-in;
  &:hover {
    color: $blue;
  }
  &:active {
    color: $blue-down;
  }
  &:focus-visible {
    outline: none;
    box-shadow: 0px 0px 2px rgba($blue, 0.8) inset;
    color: $blue;
    border-radius: 3px;
  }
}

// ------------------------------ Social
.social {
  display: flex;
  align-items: center;
  justify-content: center;
}

.social__list {
  display: flex;
  gap: 10px;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  transition: all 0.1s ease-in;
  &:hover {
    color: $blue;
  }
  &:active {
    color: $blue-down;
  }
  &:focus-visible {
    outline: none;
    box-shadow: 0px 0px 8px rgba($blue, 0.8);
    color: $blue;
    border-radius: 50%;
  }
}

@media (max-width: 940px) {
  .footer {
    background-color: $white;
  }

  .footer__wrapper {
    padding: 0;
  }

  .footer-top {
    flex-direction: column;
    gap: 0;
  }

  .footer__info,
  .footer__store {
    max-width: 100%;
  }

  .mailing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    padding: 17px 10px 41px 10px;
    border-bottom: 2px solid $gray-bg;
    background-color: #f9f9fc;
  }

  .mailing__title {
    margin-bottom: 20px;
  }

  // --------------------------------- info, store
  .info,
  .store {
    background-color: #f9f9fc;
  }

  .info__list,
  .store__list {
    transform: translateY(-100%);
    opacity: 0;
    display: none;
  }

  .footer-menu__item {
    padding: 0 11px;
    margin: 0;
  }

  .footer-menu__icon {
    display: block;
  }

  .footer-menu__link {
    padding: 4px;
  }

  .info__list_active,
  .store__list_active {
    padding: 6px 0;
    display: flex;
    flex-direction: column;
    opacity: 1;
    transform: translateY(0);
  }

  .footer__drop-down {
    padding: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid $gray-bg;
    cursor: pointer;
    &:hover {
      .footer-menu__title,
      .footer-menu__icon {
        transition: all 0.2s ease-in;
        color: $black;
      }
    }
    &:focus-visible {
      box-shadow: 0px 0px 8px rgba($blue, 0.8) inset;
      border-radius: 0;
    }
  }

  .info__drop-down_active,
  .store__drop-down_active {
    transition: all 0.5s ease-in;
    .footer-menu__title,
    .footer-menu__icon {
      color: $black;
    }
    &:hover {
      .footer-menu__title,
      .footer-menu__icon {
        color: #c4c4c4;
      }
    }
  }

  .footer-menu__title {
    color: #c4c4c4;
    margin: 0;
  }

  .footer-menu__icon {
    color: #c4c4c4;
  }

  .info__icon_active,
  .store__icon_active {
    transform: rotate(180deg);
  }

  // ------------------------------- Social
  .social {
    padding: 40px 0 0 0;
  }

  // ------------------------------- Footer-Bottom
  .footer-bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    padding: 0 10px 60px 10px;
  }
}
